import { Router, Switch, Route} from 'react-router-dom';

function App() {
  return (
    <>
    <Router>
    <Navbar/>
      <Switch>
          <Route path='./components/Header/Header.js' component={Home} />
          <Route path='./components/Projects/Projects.js' component={Projects}/>
          <Route path='./components/Contact/Contact.js' component={Contact}/>
      </Switch>
      <Footer />
    </Router>

      
    
    </>
  );
}

export default App;
